
  ---
title: 
---
<template>
  <my-wrapper title="" fit>
    <template v-slot:extra></template>
    <my-crud :fit="myPro.fixed"
             ref="crud"
             title=""
             :filter="{collapsible:true,labelWidth:'60px'}"
             :table-options="{border:true, stripe:true, rowKey:'guid'}"
             :form-options="{labelWidth:'80px'}"
             :form-dialog="{width:'50%'}"
             :view-dialog="{width:'60%', height:'60%'}"
             :columns="columns"
             :adapter="adapter">
      <!--  -->
      <template v-slot:filter>
        <my-input label="" name="name"></my-input>
        <my-select label="" name="sex" :options="[{label:'',value:1},{label:'',value:0}]"></my-select>
        <my-input label="1" name="q1" collapsible></my-input>
        <my-input label="2" name="q2" collapsible></my-input>
        <my-input label="3" name="q3" collapsible></my-input>
      </template>

      <!--  -->
      <template v-slot:toolbar>
        <el-button type="primary" icon="el-icon-plus" size="small" @click="openAddDialog"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="small" @click="batchRemove"></el-button>
      </template>

      <!-- / -->
      <template v-slot:form>
        <div>
          <my-input label="" name="name" :rules="{required:true}"></my-input>
          <my-date-picker label="" name="date" :props="{valueFormat:'yyyy-MM-dd'}"></my-date-picker>
        </div>
      </template>

      <!--  -->
      <template v-slot:detail="{row}">
        <my-detail border :column="2">
          <my-detail-item label="">{{row.name}}</my-detail-item>
          <my-detail-item label="">{{row.id}}</my-detail-item>
          <my-detail-item label="">{{row.date}}</my-detail-item>
          <my-detail-item label="">{{row.county}}</my-detail-item>
          <my-detail-item label="">{{row.info}}</my-detail-item>
        </my-detail>
      </template>

      <!--  -->
      <template v-slot:sex="{row}">
        <el-tag v-if="row.sex" size="small" type="primary"></el-tag>
        <el-tag v-else size="small" type="success"></el-tag>
      </template>

    </my-crud>
  </my-wrapper>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Tag as __Tag__} from 'element-ui';
     import {MyCrud as __MyCrud__,MyDatePicker as __MyDatePicker__,MyDetail as __MyDetail__,MyDetailItem as __MyDetailItem__,MyInput as __MyInput__,MySelect as __MySelect__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'
  import {MyCrud} from '$ui'

  export default {
    mixins: [MockForExample],
    inject: ['myPro'],
    data() {
      return {
        adapter: MyCrud.adapter.call(this, 'MockForExample'),
        columns: [
          {
            type: 'selection'
          },
          {
            label: '',
            prop: 'name'
          },
          {
            label: '',
            prop: 'id'
          },
          {
            label: '',
            prop: 'sex'
          },
          {
            label: '',
            prop: 'date'
          },
          {
            label: '',
            prop: 'county'
          },
          {
            type: 'handle',
            label: '',
            width: 200,
            handles: [
              {view: true},
              {edit: true},
              {remove: true}
            ]
          }
        ]
      }
    },
    methods: {
      openAddDialog() {
        this.$refs.crud.openAddDialog()
      },
      batchRemove() {
        this.$refs.crud.batchRemove()
      }
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Tag__)
     __vue__.use(__MyCrud__);
__vue__.use(__MyDatePicker__);
__vue__.use(__MyDetail__);
__vue__.use(__MyDetailItem__);
__vue__.use(__MyInput__);
__vue__.use(__MySelect__);
__vue__.use(__MyWrapper__)
    
  </script>

<style lang="scss" scoped>

</style>

  